<template>

  <div class="border">
    
    <h2 class="heading">Testing</h2>

    <div class="card">

      <p class="desc">Solution: {{ file }}</p>
      <p class="desc">Time Elapsed: {{ time }}</p>
      
      <table>
        <tr>
          
          <td id="text"><p id="text-child">{{ completed }}% done</p></td>

          <td id="text">
            <div :style="cont" id="progress">
              <div :style="bar" class="bar" />
            </div>
          </td>
        </tr></table>

    </div>

  </div>

</template>

<script>

import Vue from 'vue';

export default {
  name: 'TestComponent.vue',
  props: {
    file: String,
    time: String,
    completed: Number
  },
  computed: {
    bar() {
      return {
        "background-color": "#008DDD",
        height: "16px",
        "border-radius": "8px",
        width: `${this.completed - 1}%`
      }
    },
    cont() {
      return {
        background: "#004E96",
        margin: "10px",
        "padding-left": "5px",
        "padding-top": "5px",
        height: "22px",
        "border-radius": "13px",
        width: "100%",
      }
    }
  }
}

</script>

<style scoped>

#text {
  display: flex;
  justify-content: center;
  margin: 0;
}

table {
    width:100%;
    border-collapse: collapse;
}

tr {
    display: flex;
    
}
td:last-child {
    flex: 1;
    display:inline-block;
}

td:first-child {
  padding-right: 10px;
}

.desc, td {
  font-family: Cabin;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 10px;
  text-align: left;
}

.desc {
  width: 100%;
  margin-bottom: 5px;
}

.border {
  height: 30vh;
  width: 130vh;
  overflow: visible;
}

.card {
  margin: 20px;
  margin-botom: 0;
  /*height: 45%;*/
  background-color: rgba(255, 255, 255, 0.5);
  padding: 20px;
  padding-top: 5px;
  padding-bottom: 15px;
  border-radius: 5px;
}

.heading {
  font-family: Cabin;
  font-weight: bold;
  width: 100%;
  text-align: center;
  text-decoration-line: underline;
}
</style>
